@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components{

    .profile-nav-buttons{
        @apply block p-2 border border-solid border-white bg-blue-950 rounded-full cursor-pointer hover:scale-110
    }

    .edit-profile-inputs{
        @apply max-w-72 md:max-w-none bg-mid-blue rounded-full text-white py-1 px-4 border-2 border-solid border-gray-400 text-lg font-mono font-bold  focus:outline-none focus:border-original-yellow 
    }

    .edit-profile-labels{
        @apply font-mono text-sm md:text-base ml-4 font-bold
    }

    .nav-button-images{
        @apply block m-auto w-8 md:w-10
    }

    .arrow-icons{
        @apply z-998 w-8 fixed left-1 mt-4 cursor-pointer md:hidden
    }
}

@layer utilities{

    .border-b-white{
        @apply border-x-0 border-t-0 border-b border-solid border-white max-w-100
    }

    .border-t-white{
        @apply border-x-0 border-b-0 border-t border-solid border-white max-w-100
    }

    .border-r-white{
        @apply border-l-0 border-x-0 border-r-2 border-solid border-white max-w-100
    }
}

.webkit-text-white{
    -webkit-text-stroke: 1px white;
}

.webkit-text-black{
    -webkit-text-stroke: 2px black;
}
